<template>
    <div-row :obj="obj" class="content-wrapper text-picture">
        <div class="cntl">
			<span class="cntl-bar cntl-center">
				<span class="cntl-bar-fill"></span>
			</span>
            <div class="cntl-states">
                <div class="cntl-state" v-for="(item, index) in obj.imgTextBox" :key="index">
                    <div class="cntl-image" :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                        <div class="cnt1-image-text">
                            <img :src="item.imgUrl">
                            <h4 class="info" v-html="item.info"></h4>
                        </div>
                    </div>
                    <div class="fl-right">
                        <div class="cntl-content" :class="obj.animateTitle? 'wow '+obj.animateTitle : ''">
                            <h4 v-html="item.title"></h4>
                            <p v-html="item.desc"></p>                      
                        </div>
                    </div>                  
                    <div class="cntl-icon cntl-center">{{index}}</div>
                </div>
            </div>
        </div>
    </div-row>
</template>
<script>
import animate from '@/mixins/animate'
export default {
    name: 'ThreeColumns',
    mixins: [animate]
    
}
</script>
<style lang="less" scoped>
    .content-wrapper{
        position: relative;
        .cntl {
            position: relative;
            width: 100%;
            overflow: hidden;
        }

        .cntl-center {
            left:0;
            right:0;
            margin-left:auto;
            margin-right:auto;
        }

        .cntl-bar {
            position: absolute;
            width: 10px;
            top: 0;
            bottom: 0;
            background-color: #009ABB;
            box-shadow: inset 0px 0px 7px -2px #000;
        }


        .cntl-bar-fill {
            background-color: #009ABB;
            position: absolute;
            left:0;
            right:0;
            top:0;
            height:0;

        }

        .cntl-state {
            position: relative;
            width:100%;
            min-height: 200px;
            margin-bottom: 50px;
        }

        .cntl-state::after {
            display:block;
            content: ' ';
            clear:both;
        }

        .cntl-icon {
            border-radius: 50%;
            width: 60px;
            height: 60px;
            background-color: #00313C;
            border: solid 3px #009ABB;
            box-shadow: 0px 0px 19px -9px #000;
            position: absolute;
            top: 0;
            text-align: center;
            line-height: 60px;
            font-size: 40px;
            color: #fff;
        }
        .fl-right{
            float: left;
            width: 50%;
            padding: 30px 0px 20px 120px;
            // margin-left: 2%;
        }
        .cntl-content {
            width: 60%;
            padding: 20px;
            background-color: rgba(238, 238, 238, 0.25);
            border-radius: 8px;
            border-bottom: solid 3px #009ABB;
            float:left;
            // opacity:0;
            position:relative;
            // margin-left:-40%;
        }

        // .cntl-state:nth-child(2n+2) .cntl-content {
        //     float:right;
        //     // margin-right:-40%;
        // }

        .cntl-image {
           
            float:left;
            // opacity:0;
            width: 50%;
            padding: 30px 120px 20px 0;
            // padding: 2%;
            .cnt1-image-text{
                float:right;
                width: 40%;
                text-align: center;
                .info{
                    padding: 5px 0;
                }
            }
        }

        // .cntl-state:nth-child(2n+1) .cntl-image {
        //     float:right;
        // }

        .cntl-image img {
            width:100%;
            margin: 0 auto;
            border-radius: 50%;
        }

        .cntl-content h4 {
            font-size:20px;
            font-weight: normal;
            margin-bottom: 10px;
        }


        /*
        animations
        */
        .cntl-bar-fill,.cntl-content,.cntl-image {
            -webkit-transition: all 500ms ease;
            -moz-transition: all 500ms ease;
            -o-transition: all 500ms ease;
            -ms-transition: all 500ms ease;
            transition: all 500ms ease;
        }

        .cntl-state:nth-child(2n+2).cntl-animate .cntl-content {
            margin-right:0%;
        }

        .cntl-animate .cntl-content {
            opacity:1;
            margin-left:0%;
        }

        .cntl-animate .cntl-image {
            opacity:1;
        }




        /* mobile support */

        @media (max-width: 600px) {

            .cntl-bar {
                left:auto;
                right: 37px;
            }

            .cntl-content {
                width:80%;
                float:none;
            }
            .cntl-state:nth-child(2n+2) .cntl-content {
                margin-right:0%;
                float:none;
            }

            .cntl-image {
                width:80%;
            }

            .cntl-state:nth-child(2n+1) .cntl-image {
                float:none;
            }

            .animate .cntl-content {
                margin-left: 2%;
            }

            .cntl-icon {
                left: auto;
                right: 0;
            }

        }
    }
</style>



